import React, { Component } from 'react';
import ReactDOM from 'react-dom'

import './NavbarLink.css'

const ELEMENT_IDS = ['profile-container', 'personal-container', 'areas-interest-container', 'portfolio-container', 'contact-container']
const TITLE_Y_OFFSET = 100

class NavbarLink extends Component{
    constructor(props){
        super(props)

        this.handleClick = this.handleClick.bind(this)
        this.elementID = this.props.id
        this.linkElement = document.getElementById(this.elementID)
    }

    componentDidMount(){
        ReactDOM.findDOMNode(this).addEventListener('click', this.handleClick)
    }

    componentWillUnmount(){
        ReactDOM.findDOMNode(this).removeEventListener('click', this.handleClick)
    }

    handleClick = (event) => {
        event.preventDefault()

        let elementToScroll = document.getElementById(ELEMENT_IDS[this.props.index])
        let scrollValue = elementToScroll.offsetTop-TITLE_Y_OFFSET

        window.scrollTo({
            top: scrollValue,
            behavior: 'smooth'
        })
        
        this.props.showMenuMethod()
    } 

    render(){
        return(
            <li><button id={this.elementID} className="link">{this.props.name}</button></li>
        );
    }
}

export default NavbarLink;